(function () {
  class CacheBuddy extends HTMLElement {
    constructor() {
      super();
      this._props = {};
      this.attachShadow({ mode: "open" });
      this.shadowRoot.innerHTML = `<div style="padding: 16px; background-color: #f0f4f8; border-radius: 8px; font-family: Arial, sans-serif; max-width: 400px;">
  <h2 style="margin-top: 0; color: #0d72ff;">CacheBuddy</h2>
  <p style="margin: 8px 0; color: #333;">
    Hide this widget, and you can use <strong>CacheBuddy</strong> methods in your code.
  </p>
</div>`;
      this._localStorage = localStorage || window.localStorage;
    }

    // Set methods (already return boolean)
    setNumberCacheMemory(id, value) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      if (
        (typeof value !== "number" || !Number.isFinite(value)) &&
        value !== null
      ) {
        console.error(
          "CacheBuddy: Value must be a number for setNumberCacheMemory"
        );
        return false;
      }
      if (!id) {
        console.error(
          "CacheBuddy: Invalid parameters for setNumberCacheMemory"
        );
        return false;
      }
      this._localStorage.setItem(id, value);
      return true;
    }

    setStringCacheMemory(id, value) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      if (typeof value !== "string") {
        console.error(
          "CacheBuddy: Value must be a string for setStringCacheMemory"
        );
        return false;
      }
      if (!id) {
        console.error(
          "CacheBuddy: Invalid parameters for setStringCacheMemory"
        );
        return false;
      }
      this._localStorage.setItem(id, value);
      return true;
    }

    setBooleanCacheMemory(id, value) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      if (typeof value !== "boolean") {
        console.error(
          "CacheBuddy: Value must be a boolean for setBooleanCacheMemory"
        );
        return false;
      }
      if (!id) {
        console.error(
          "CacheBuddy: Invalid parameters for setBooleanCacheMemory"
        );
        return false;
      }
      this._localStorage.setItem(id, value);
      return true;
    }

    setSelectionCacheMemory(id, value) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      if (typeof value !== "object" || value === null || Array.isArray(value)) {
        console.error(
          "CacheBuddy: Value must be a non-null object (not array) for setSelectionCacheMemory"
        );
        return false;
      }
      if (!id) {
        console.error(
          "CacheBuddy: Invalid parameters for setSelectionCacheMemory"
        );
        return false;
      }
      this._localStorage.setItem(id, JSON.stringify(value));
      return true;
    }

    setArrayCacheMemory(id, value) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      if (!Array.isArray(value)) {
        console.error(
          "CacheBuddy: Value must be an array for setArrayCacheMemory"
        );
        return false;
      }
      if (!id) {
        console.error("CacheBuddy: Invalid parameters for setArrayCacheMemory");
        return false;
      }
      this._localStorage.setItem(id, JSON.stringify(value));
      return true;
    }

    getSelectionCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      const value = this._localStorage.getItem(id);
      if (value === null) return null;
      try {
        const obj = JSON.parse(value);
        if (typeof obj === "object" && obj !== null && !Array.isArray(obj)) {
          return obj;
        }
        return null;
      } catch {
        return null;
      }
    }

    getArrayCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      const value = this._localStorage.getItem(id);
      if (value === null) return [];
      try {
        const arr = JSON.parse(value);
        if (Array.isArray(arr)) {
          return arr;
        }
        return [];
      } catch {
        return [];
      }
    }

    // New Get methods corresponding to above sets:

    getNumberCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      const value = this._localStorage.getItem(id);
      if (value === null) return null;
      const numberVal = Number(value);
      return isNaN(numberVal) ? null : numberVal;
    }

    getStringCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      return this._localStorage.getItem(id);
    }

    getBooleanCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      const value = this._localStorage.getItem(id);
      if (value === null) return null;
      return value === "true";
    }

    getSelectionCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      const value = this._localStorage.getItem(id);
      if (value === null) return null;
      try {
        const obj = JSON.parse(value);
        if (typeof obj === "object" && obj !== null && !Array.isArray(obj)) {
          return obj;
        }
        return null;
      } catch {
        return null;
      }
    }

    getArrayCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      const value = this._localStorage.getItem(id);
      if (value === null) return [];
      try {
        const arr = JSON.parse(value);
        if (Array.isArray(arr)) {
          return arr;
        }
        return [];
      } catch {
        return [];
      }
    }

    // Other existing methods unchanged ...
    existCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      return this._localStorage.getItem(id) !== null;
    }

    deleteCacheMemory(id) {
      id = "CacheBuddy."+id; // Prefixing id for consistency
      if (!id) {
        console.error("CacheBuddy: Invalid parameter for deleteCacheMemory");
        return false;
      }
      if (this._localStorage.getItem(id) !== null) {
        this._localStorage.removeItem(id);
        return true;
      } else {
        console.warn(
          `CacheBuddy: No cache memory found with id ${id} to delete.`
        );
        return false;
      }
    }

    deleteAllCacheMemory() {
      try {
        const keysToDelete = [];
        for (let i = 0; i < this._localStorage.length; i++) {
          const key = this._localStorage.key(i);
          if (key && key.startsWith("CacheBuddy.")) {
            keysToDelete.push(key);
          }
        }

        for (const key of keysToDelete) {
          this._localStorage.removeItem(key);
        }

        return true;
      } catch (error) {
        console.error("CacheBuddy: Failed to delete keys.", error);
        return false;
      }
    }

   getAllCacheMemory() {
      const cacheBuddyData = {};
      for (let i = 0; i < this._localStorage.length; i++) {
        const key = this._localStorage.key(i);
        if (key && key.startsWith("CacheBuddy.")) {
          const strippedKey = key.replace("CacheBuddy.", "");
          let value = this._localStorage.getItem(key);
          try {
            value = JSON.parse(value); // attempt to parse JSON (for arrays, objects, numbers, booleans)
          } catch {
            // if parsing fails, keep it as string
          }
          cacheBuddyData[strippedKey] = value;
        }
      }
      return cacheBuddyData;
    }
  }
  customElements.define("com-rohitchouhan-sap-cachebuddy", CacheBuddy);
})();
